.figure {
  display: flex;
  justify-content: center;
  align-items: center;

  .circle {
    display: flex;
    border-radius: 50%;
    width: 150px;
    height: 150px;
    background: rgb(113, 228, 113);
    justify-content: center;
    align-items: center;

    .rectangle {
      width: 60px;
      height: 40px;
      border: 8px solid white;
      border-right: none;
      border-top: none;
      transform: rotate(-50deg);
      margin-bottom: 30px;
    }
  }
}

.welcome-btn {
  // max-height: fit-content;
  display: flex;
  flex-direction: column;
  // width: 50%;
  align-items: center;
  text-align: center;
  // padding-top: 50px;
}

.welcome-btn a {
  width: 50%;
  padding: 10px;

  button {
    width: 100%;
  }
}
